/* Add import Source Sans Pro font: Regular 400, SemiBold 600, Bold: 700  */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;500;700&family=Source+Sans+Pro:wght@400;600;700&display=swap');

html,
body,
#root{
  height: 100%;
  width: 100%;

  font-family: 'Source Sans Pro', sans-serif;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #e0e0e0;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
